import {
	BrowserRouter as Router,
	Switch,
	Route,
	Redirect,
} from 'react-router-dom';
import { createGlobalStyle } from 'styled-components';
import SignIn from './components/SignIn';
import SignUp from './components/SignUp';
import Home from './components/Home';
import PageNotFound from './components/StyledComponents/PageNotFound';

const Global = createGlobalStyle`
* {
	box-sizing: border-box;
  margin:0;
  padding: 0;
}
body {
	background-color: rgb(18,	18,	18	);
	color: white;
	font-family: Roboto;
}
`

function App() {
	const registered = JSON.parse(localStorage.getItem('registered'))
	return (
		<Router>
			<Global />
			<Switch>
				<Route exact path="/homework-21-react-styled-components">
					{registered ? (
						<Redirect to="/homework-21-react-styled-components/sign-in" />
					) : (
						<Redirect to="/homework-21-react-styled-components/sign-up" />
					)}
				</Route>
				<Route path="/homework-21-react-styled-components/home" component={Home} />
				<Route
					path="/homework-21-react-styled-components/sign-in"
					component={SignIn}
				/>
				<Route
					path="/homework-21-react-styled-components/sign-up"
					component={SignUp}
				/>

				<Route path="*" component={PageNotFound} />
			</Switch>
		</Router>
	)
}

export default App;